@tailwind base;
@tailwind components;

.bg-footer-top {
  background-image: url("./assets/download Shape.svg");
  @apply bg-red-700;
}

.bg-footer-bottom {
  background-image: url("./assets/footer abstract design.svg");
  @apply bg-blue-900;
}

@media (min-width: 768px) {
  .bg-body {
    background-repeat: no-repeat;
    background: url("./assets/body circles.svg") no-repeat -600px 700px,
      url("./assets/body circles.svg") no-repeat 300% -500px;
  }
}

.btn {
  @apply cursor-pointer inline-block py-4 px-6 text-white rounded-full font-medium uppercase;
}

.btn-primary {
  background-image: linear-gradient(285deg, #f00b51, #e20082);
}

.dropdown {
  @apply relative;
}

.dropdown-menu {
  @apply absolute bg-white p-12 rounded shadow-lg mt-4;
  width: 400px;
  left: calc(50% - 15px);
  transform: translate(-50%, 0);
  display: none;
}

.dropdown-menu.show {
  display: block;
}

.dropdown-menu::after {
  content: '';
  position: absolute;
  top: calc(-10px / 2);
  z-index: -1;
  left: 50%;
  width: 10px;
  height: 10px;
  background: #fff;
  transform: translate(-50%, 0) rotate(45deg);
  box-shadow: 0 0 15px 2px rgba(0,0,0,0.1)
}

.dropdown-menu::before {
  content: '';
  position: absolute;
  top: 0px;
  z-index: 2;
  left: 50%;
  width: 80px;
  height: 20px;
  background: #fff;
  transform: translate(-50%, 0);
}

#navMenu {
  transform: translate(100%, 0);
  transition: 0.2s;
}

@media (min-width: 768px) {
  #navMenu {
    transform: translate(0, 0);
  }
}

#navMenu.active {
  transition: 0.4s ease;
  transform: translate(0, 0);
}

.btn:focus {
  @apply outline-none;
}

.close {
  @apply absolute cursor-pointer p-2;
  top: 0.5rem;
  right: 0.5rem;
}

@tailwind utilities;

.bg-image-none {
  background-image: none;
}

@responsive {
  .bg-image-none {
    background-image: none;
  }
}
